<template>
  <div class="page-search">
    <!-- <van-nav-bar title="查询" :left-arrow="isInnerPage" @click-left="onClickNavBarLeft" :fixed="true" /> -->
    <div class="main-content">
      <van-sticky :offset-top="0">
        <form action="/" class="form-search">
          <van-search v-model="keyWord" placeholder="请输入搜索关键词" show-action @search="doSearch">
            <div slot="action" @click="doSearch">搜索</div>
          </van-search>
        </form>
      </van-sticky>
      <div class="search-datas-wrapper">
        <div class="search-datas">
          <van-pull-refresh v-model="isRefreshing" @refresh="onRefresh">
            <van-list
              v-model="isLoading"
              :finished="loadFinished"
              :immediate-check="false"
              :offset="offset"
              :error.sync="error"
              error-text="请求失败，点击重新加载"
              finished-text="我是有底线的"
              @load="onLoad"
            >
              <div v-if="datas.length > 0">
                <div v-for="(item, index) in datas" :key="item.sfzmhm + index" class="item-content">
                  <div class="field">
                    <span class="car-number">{{ item.hphm }}</span>
                  </div>
                  <div class="field">
                    <span class="key">持有人：</span>
                    <span class="value">{{ item.syr }}</span>
                  </div>
                  <div class="field">
                    <span class="key">身份证号：</span>
                    <span class="value">{{ item.sfzmhm }}</span>
                  </div>
                  <van-divider :style="{ color: '#bfbfbf', borderColor: '#bfbfbf', padding: '0 0' }" />
                </div>
              </div>
            </van-list>
          </van-pull-refresh>
        </div>
        <div v-if="datas.length == 0" class="empty">空空如也...</div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import qs from 'querystring'
import { mapState, mapActions } from 'vuex'
import { Toast, Search, PullRefresh, List, Divider, Sticky } from 'vant'
import request from '@/utils/request'

Vue.use(Toast)
  .use(Search)
  .use(PullRefresh)
  .use(List)
  .use(Divider)
  .use(Sticky)

export default {
  name: 'search',
  // props: ['carNum'],
  components: {},
  data() {
    return {
      isInnerPage: false,
      keyWord: this.$route.params.carNum || '',
      //
      isRefreshing: false, // 下拉刷新
      //
      isLoading: false, // 上拉加载
      error: false,
      loadFinished: false, // 上拉加载完毕
      offset: 100, // 滚动条与底部距离小于 offset 时触发load事件
      pageSize: 10, // 每页条数
      pageIndex: 1, // 页码
      datas: [] // 请求数据
    }
  },

  computed: {},

  created() {
    if (this.$route.params.carNum) {
      this.isInnerPage = true
    }
  },
  mounted() {
    this.getDatas()
  },
  methods: {
    // 关键字搜索
    doSearch() {
      window.scrollTo(0, 0)
      this.pageIndex = 1
      this.loadFinished = false // 不写这句会导致你上拉到底过后在下拉刷新将不能触发下拉加载事件
      this.getDatas() // 加载数据
    },
    // 下拉刷新
    onRefresh() {
      this.pageIndex = 1
      this.loadFinished = false // 不写这句会导致你上拉到底过后在下拉刷新将不能触发下拉加载事件
      this.getDatas() // 加载数据
    },
    // 下拉加载
    onLoad() {
      this.pageIndex++
      this.getDatas()
    },
    // 获取数据
    getDatas() {
      const _self = this
      const params = {
        pageNo: _self.pageIndex,
        pageSize: _self.pageSize,
        searchStr: _self.keyWord
      }
      request(`/api/nonMotorInfo/findByPage?${qs.stringify(params)}`, {
        method: 'POST'
      })
        .then(response => {
          if (response.resultCode === 200) {
            const rows = response.data.data
            if (rows == null || rows.length === 0) {
              // 加载结束
              _self.loadFinished = true
              return
            }
            if (rows.length < _self.pageSize) {
              // 最后一页不足10条的情况
              _self.loadFinished = true
            }
            // 处理数据
            if (_self.pageIndex === 1) {
              _self.datas = rows
            } else {
              _self.datas = _self.datas.concat(rows)
            }
          } else {
            // 加载结束
            _self.loadFinished = true
            // Toast.fail(response.message)
          }
        })
        .catch(() => {
          this.error = true
        })
        .finally(() => {
          _self.isRefreshing = false
          _self.isLoading = false
        })
    }
  }
}
</script>

