    .empty {
      display: flex;
      color: #456fe4;
      font-size: 18px;
      justify-content: center;
      min-height: 80vh;
      align-items: center;
    }
    .search-datas {
      padding: 0 15px;
      .item-content {
        .field {
          height: 30px;
          line-height: 30px;
          .key {
            font-size: 14px;
            color: @gray-darker;
          }
          .value {
            font-size: 14px;
            color: @black;
          }
          .car-number {
            color: #456fe4;
            font-size: 18px;
          }
        }
      }
    }
  }
}
</style>
